<script setup>
import { ref, reactive, defineProps } from "vue";
import { required, helpers } from "@vuelidate/validators";
import { useVuelidate } from "@vuelidate/core";
import * as CommonFunctions from "../../common/ValidationResponseFormatter";

const props = defineProps({
  close: {
    type: Function,
  },
});

const data = reactive({
  FirstName: "",
  LastName: "",
  Username: "",
  NIC: "",
  Status: "",
  Roles: [],
  NGOs: [],
});

const NGOList = ref([
  {
    Name: "Caritas",
    logo: "https://www.caritas.org/wordpress/wp-content/uploads/2017/05/logo-Caritas-Mauritius.gif",
  },
  {
    Name: "Enn Rev Enn Sourire",
    logo: "https://www.ennrevennsourir.org/wp-content/themes/crowdfunding/images/logo.png",
  },
  {
    Name: "Yuva",
    logo: "https://i0.wp.com/yuva.info/wp-content/uploads/2022/06/YUVA-website-logo-topper.jpg?resize=768%2C286&ssl=1",
  },
  {
    Name: "Pils",
    logo: "https://pils.mu/wp-content/uploads/2022/01/pils-logo.png",
  },
]);

const submitted = ref(false);

const checkString = (value) => {
  var regexVal = /[A-Za-z]+$/;
  if (regexVal.test(value)) {
    return true;
  } else {
    return false;
  }
};

const rules = {
  FirstName: {
    required: helpers.withMessage("First name is required", required),
    checkString: helpers.withMessage("First name is invalid", checkString),
  },
  LastName: {
    required: helpers.withMessage("Last name is required", required),
    checkString: helpers.withMessage("Last name is invalid", checkString),
  },
  Username: {
    required: helpers.withMessage("Username is required", required),
    checkString: helpers.withMessage("Username is invalid", checkString),
  },
  NIC: {
    required: helpers.withMessage("NIC is required", required),
    checkString: helpers.withMessage("NIC is invalid", checkString),
  },
  Status: {
    required: helpers.withMessage("Status is required", required),
  },
  Roles: {
    required: helpers.withMessage("Roles are required", required),
  },
  NGOs: {},
};

const v$ = useVuelidate(rules, data);

const submit = async (isFormValid) => {
  submitted.value = true;
  console.log(data);
  if (!isFormValid) {
    return;
  }

  let obj = JSON.parse(JSON.stringify(data));
  console.log(obj);
  event.preventDefault();
  clearForm();
  props.close();
};

const clearForm = () => {
  for (const key in data) {
    data[key] = "";
  }

  submitted.value = false;
};
</script>
<template>
  <form @submit.prevent="submit(!v$.$invalid)">
    <div class="grid">
      <div class="col-12 lg:col-6">
        <label for="FirstName" class="block text-900 font-medium mb-2"
          >First name</label
        >
        <InputText
          id="FirstName"
          type="text"
          v-model="v$.FirstName.$model"
          placeholder="First name"
          :class="[
            {
              'p-invalid': v$.FirstName.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
        />
        <small v-if="v$.FirstName.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.FirstName)
        }}</small>
      </div>
      <div class="col-12 lg:col-6">
        <label for="LastName" class="block text-900 font-medium mb-2"
          >Last name</label
        >
        <InputText
          id="LastName"
          type="text"
          v-model="v$.LastName.$model"
          placeholder="Last name"
          :class="[
            {
              'p-invalid': v$.LastName.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
        />
        <small v-if="v$.LastName.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.LastName)
        }}</small>
      </div>
      <div class="col-12 lg:col-6">
        <label for="Username" class="block text-900 font-medium mb-2"
          >Username</label
        >
        <InputText
          id="Username"
          type="text"
          v-model="v$.Username.$model"
          placeholder="Username"
          :class="[
            {
              'p-invalid': v$.Username.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
        />
        <small v-if="v$.Username.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.Username)
        }}</small>
      </div>
      <div class="col-12 lg:col-6">
        <label for="NIC" class="block text-900 font-medium mb-2">NIC</label>
        <InputText
          id="NIC"
          type="text"
          v-model="v$.NIC.$model"
          placeholder="NIC"
          :class="[
            {
              'p-invalid': v$.NIC.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
        />
        <small v-if="v$.NIC.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.NIC)
        }}</small>
      </div>
      <div class="col-12 lg:col-6">
        <label for="Status" class="block text-900 font-medium mb-2"
          >Status</label
        >
        <Dropdown
          id="Status"
          :options="[true, false]"
          v-model="v$.Status.$model"
          placeholder="Select Status"
          :class="[
            {
              'p-invalid': v$.Status.$invalid && submitted,
              'w-full mb-2': true,
            },
          ]"
        />
        <small v-if="v$.Status.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.Status)
        }}</small>
      </div>
      <div class="col-12 lg:col-6">
        <label for="Roles" class="block text-900 font-medium mb-2">Roles</label>
        <MultiSelect
          v-model="v$.Roles.$model"
          :options="['Admin', 'Editor', 'Agent']"
          placeholder="Select roles"
          display="chip"
          class="w-full"
        >
          <template #option="slotProps">
            <div class="flex align-items-center">
              <div>{{ slotProps.option }}</div>
            </div>
          </template>
          <template #footer>
            <div class="py-2 px-3">
              <b>{{ RolesVal ? RolesVal.length : 0 }}</b>
              item{{ (RolesVal ? RolesVal.length : 0) > 1 ? "s" : "" }}
              selected.
            </div>
          </template>
        </MultiSelect>
        <small v-if="v$.Roles.$invalid && submitted" class="p-error">{{
          CommonFunctions.ValidationResponseFormatter(v$.Roles)
        }}</small>
      </div>
      <div class="col-12">
        <label for="Roles" class="block text-900 font-medium mb-2">NGOs</label>
        <MultiSelect
          v-model="v$.NGOs.$model"
          :options="NGOList"
          optionLabel="Name"
          placeholder="Select NGOs"
          display="chip"
          class="w-full"
          filter
        >
          <template #option="slotProps">
            <div class="flex align-items-center">
              <img
                :alt="slotProps.option.Name"
                :src="slotProps.option.logo"
                class="mr-2"
                style="width: 18px"
              />
              <div>{{ slotProps.option.Name }}</div>
            </div>
          </template>
          <template #footer>
            <div class="py-2 px-3">
              <b>{{ NGOsVal ? NGOsVal.length : 0 }}</b>
              item{{ (NGOsVal ? NGOsVal.length : 0) > 1 ? "s" : "" }}
              selected.
            </div>
          </template>
        </MultiSelect>
      </div>
      <div class="col-12">
        <Button
          label="Save"
          icon="pi pi-save"
          class="w-full p-3"
          type="submit"
        ></Button>
      </div>
    </div>
  </form>
</template>
