import { defineStore } from "pinia";
import { reactive } from "vue";

export const useUserDataStore = defineStore({
  id: "useUserDataStore",
  state: () => ({
    state: reactive({
      userData: null,
    }),
  }),
  actions: {
    saveUserdata(Obj) {
      this.state.userData = Obj;
    },
  },
  getters: {
    getUserData() {
      return this.state.userData;
    },
  },
});
