<script setup>
import { ref, defineProps } from "vue";
import { FilterMatchMode, FilterOperator } from "primevue/api";
import { useRouter } from "vue-router";
import UserForm from "../../components/UserForm/UserForm";
import { useUserDataStore } from "@/store/useStore";

import * as XLSX from "xlsx";
// import jsPDF from "jspdf";
// import html2canvas from "html2canvas";

const filters = ref();
const IsExcelDownloading = ref(false);
const router = useRouter();
const store = useUserDataStore();
const CreateModalVisibility = ref(false);

const initFilters = () => {
  filters.value = {
    global: { value: null, matchMode: FilterMatchMode.CONTAINS },
    FirstName: {
      operator: FilterOperator.AND,
      constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
    },
    LastName: {
      operator: FilterOperator.AND,
      constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
    },
    Username: {
      operator: FilterOperator.AND,
      constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
    },
    NIC: {
      operator: FilterOperator.AND,
      constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
    },
    IsUserActive: {
      operator: FilterOperator.OR,
      constraints: [{ value: null, matchMode: FilterMatchMode.EQUALS }],
    },
  };
};
initFilters();

const props = defineProps({
  data: {
    type: Array,
  },
});

const clearFilter = () => {
  initFilters();
};

const exportTableToXLSX = async () => {
  IsExcelDownloading.value = true;
  const table = document.getElementById("exportTable");
  const ws = XLSX.utils.table_to_sheet(table);
  const wb = XLSX.utils.book_new();
  await XLSX.utils.book_append_sheet(wb, ws, "Sheet 1");
  const currentDate = new Date();
  const uidentifier =
    currentDate.getDay() +
    "" +
    currentDate.getMonth() +
    "" +
    currentDate.getFullYear() +
    "" +
    currentDate.getHours() +
    "" +
    currentDate.getMinutes() +
    "" +
    currentDate.getSeconds();

  await XLSX.writeFile(wb, "userList_" + uidentifier + ".xlsx");
  IsExcelDownloading.value = false;
};

const selectedUser = (data) => {
  var user = JSON.parse(JSON.stringify(data));
  store.saveUserdata(user);
  router.push({
    name: "userinfo",
  });
};

const closeModal = () => {
  CreateModalVisibility.value = false;
};
</script>
<template>
  <div class="card">
    <div class="grid">
      <div class="col-12">
        <Button
          type="button"
          icon="pi pi-plus"
          label="Create User Access"
          style="float: right"
          @click="CreateModalVisibility = true"
        />
      </div>
    </div>
    <DataTable
      id="exportTable"
      v-model:filters="filters"
      :value="props.data"
      paginator
      showGridlines
      :rows="5"
      dataKey="NIC"
      filterDisplay="menu"
      :loading="loading"
      :globalFilterFields="[
        'FirstName',
        'LastName',
        'Username',
        'NIC',
        'IsUserActive',
      ]"
      :rowsPerPageOptions="[5, 10, 25]"
    >
      <template #header>
        <div class="flex justify-content-between">
          <Button
            type="button"
            icon="pi pi-filter-slash"
            label="Clear"
            outlined
            @click="clearFilter()"
          />
          <span class="p-input-icon-left">
            <i class="pi pi-search" />
            <InputText
              v-model="filters['global'].value"
              placeholder="Keyword Search"
            />
          </span>
        </div>
      </template>
      <template #paginatorstart>
        <Button type="button" icon="pi pi-refresh" text />
      </template>
      <template #paginatorend>
        <Button
          label="Excel"
          @click="exportTableToXLSX"
          text
          :loading="IsExcelDownloading"
          rounded
          icon="pi pi-file-excel"
        />
      </template>
      <template #empty> No customers found. </template>
      <template #loading> Loading customers data. Please wait. </template>
      <Column field="FirstName" header="FirstName" style="width: 15%">
        <template #body="{ data }">
          {{ data.FirstName }}
        </template>
        <template #filter="{ filterModel }">
          <InputText
            v-model="filterModel.value"
            type="text"
            class="p-column-filter"
            placeholder="Search by FirstName"
          />
        </template>
      </Column>
      <Column field="LastName" header="LastName" style="width: 15%">
        <template #body="{ data }">
          {{ data.LastName }}
        </template>
        <template #filter="{ filterModel }">
          <InputText
            v-model="filterModel.value"
            type="text"
            class="p-column-filter"
            placeholder="Search by LastName"
          />
        </template>
      </Column>
      <Column field="Username" header="Username" style="width: 15%">
        <template #body="{ data }">
          {{ data.Username }}
        </template>
        <template #filter="{ filterModel }">
          <InputText
            v-model="filterModel.value"
            type="text"
            class="p-column-filter"
            placeholder="Search by Username"
          />
        </template>
      </Column>
      <Column field="NIC" header="NIC" style="width: 20%">
        <template #body="{ data }">
          {{ data.NIC }}
        </template>
        <template #filter="{ filterModel }">
          <InputText
            v-model="filterModel.value"
            type="text"
            class="p-column-filter"
            placeholder="Search by NIC"
          />
        </template>
      </Column>
      <Column
        header="Status"
        field="IsUserActive"
        :filterMenuStyle="{ width: '14rem' }"
        style="width: 15%"
      >
        <template #body="{ data }">
          <Tag
            :value="data.IsUserActive"
            :severity="data.IsUserActive ? 'success' : 'danger'"
          />
        </template>
        <template #filter="{ filterModel }">
          <Dropdown
            v-model="filterModel.value"
            :options="[true, false]"
            placeholder="Select One"
            class="p-column-filter"
            showClear
          >
            <template #option="slotProps">
              <Tag :value="slotProps.option" />
            </template>
          </Dropdown>
        </template>
      </Column>
      <Column field="Actions" header="" style="width: 8%">
        <template #body="{ data }">
          <div class="grid">
            <div class="col-12" style="text-align: center">
              <Button
                icon="pi pi-eye"
                severity="secondary"
                rounded
                outlined
                @click="selectedUser(data)"
                aria-label="Submit"
              />
            </div>
          </div>
        </template>
      </Column>
    </DataTable>
  </div>
  <div class="card flex justify-content-center">
    <Dialog
      v-model:visible="CreateModalVisibility"
      modal
      header="Header"
      :style="{ width: '50rem' }"
      :breakpoints="{ '1199px': '75vw', '575px': '90vw' }"
    >
      <template #header>
        <div
          class="inline-flex align-items-center justify-content-center gap-2"
        >
          <span class="font-bold white-space-nowrap">Create User Access</span>
        </div>
      </template>
      <UserForm :close="closeModal" />
    </Dialog>
  </div>
</template>
